.wix-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
}

.wix-header {
  padding: 20px;
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  
  h2 {
    margin: 0;
    color: #333;
  }
}

.wix-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}
